import swal from "sweetalert2"
const api_server_location = "https://stark-bastion-43602.herokuapp.com/api"

function processStatus(response) {
    //  "0"  (Cordova/Phonegap)
    if (response.status === 200 || response.status === 0) {
        return Promise.resolve(response)
    } else {
        return Promise.reject(new Error(response.statusText))
    }
}

/* 
#gender: :1;
#age: 
#arrival_way: :1
#OHCA: :0 ; :1
#ecode_type: 1~12 , , , , , , , , , , , 
*/

document.querySelector('#calculate3').onclick = function(){
    swal.setDefaults({
        animation: false,
        customClass: 'animated slideInRight',
        confirmButtonText: '',
        cancelButtonText: '',
        showCancelButton: true,
        progressSteps: ['','', '', '<i class="fa fa-heartbeat" aria-hidden="true"></i>',''],
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#aaa',
    })
          
    // const reason = ["nouse","", ""]
    // let reasonObject = Object.assign({}, reason);
    // delete reasonObject['0'];
    let reasonObject = {
        'car':"",
        'fall':""
    }
        
    let ageArray = Array.apply(null, Array(100)).map((x, i) => i);
    let ageObject = Object.assign({}, ageArray);
    delete ageObject['0'];

    let weekdayArray = [0,1,2,3,4,5,6,7];
    let weekObject = Object.assign({}, weekdayArray);
    delete weekObject['0'];

    let steps = [
        {
            title: 'ISS',
            title: '',
            input: 'select',
            inputOptions: reasonObject,
            animation: false,
            customClass: 'animated slideInRight'
        },
        {
            title: '',
            input: 'select',
            inputOptions: {
                '1': '',
                '2': '',
            },
            animation: false,
            customClass: 'animated slideInRight'
        },{
            title: '',
            input: 'select',
            inputOptions: ageObject,
            animation: false,
            customClass: 'animated slideInRight'
        },{
            title: 'OHCA',
            text: '',
            input: 'select',
            inputOptions: {
                '0': '',
                '1': '',
            },
            animation: false,
            customClass: 'animated slideInRight',
        },{
            title: '_',
            text: '',
            input: 'select',
            inputOptions: weekObject,
            animation: false,
            customClass: 'animated slideInRight'
        }
    ]
        
    swal.queue(steps).then((result) => {
        swal.resetDefaults()
        if (result.value) {
            swal({
                title: 'ISS16',
                showCancelButton: true,
                confirmButtonText: '',
                cancelButtonText: '',
                animation: false,
                customClass: 'animated zoomIn',
                showLoaderOnConfirm: true,
                preConfirm: () => {
                    return new Promise((resolve,reject) => {
                    let answerArray = result.value;
                    //insert 1 at arrival1
                    answerArray.splice(3, 0, 1);
                    const payload = {
                        "ecode_type": answerArray[0],
                        "gender":parseInt(answerArray[1]),
                        "age":parseInt(answerArray[2]),
                        "arrival_1":answerArray[3],
                        "OHCA": parseInt(answerArray[4]),
                        "weekday": parseInt(answerArray[5]),
                    }
                    const data = JSON.stringify(payload);
                    // console.log(data);
                    fetch(api_server_location+"/models/iss",{
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        },
                        method: "POST",
                        body: data
                    })
                    .then(processStatus)
                    .then(
                        function (response) {
                            response.json()
                            .then((data)=>{
                                let predictionResult = data.prediction;
                                resolve(predictionResult);
                            }).catch((err)=>{
                                // console.log('ParseError', err);
                                reject(err)
                            });
                        }
                    )
                    .catch(function (err) {
                        // console.log('Fetch Error :-S', err);
                        reject(err)
                    });
                    
                    // setTimeout(() => {
                    //   if ("abs" === 'taken@example.com') {
                    //     swal.showValidationError(
                    //       'This email is already taken.'
                    //     )
                    //   }
                    //   resolve(answer)
                    // }, 2000)
                    })
                },
                allowOutsideClick: () => !swal.isLoading()
                })
                .then((result) => {
                    // console.log(result);
                    if (result.value) {
                        // let showType = ""
                        // let showText = ""
                        // const isOverSixteen = result.value === 1 ? true: false
                        // if(isOverSixteen){
                        //     showType = 'warning'
                        //     showText = "ISS16!!!!";
                        // }else{
                        //     showType = 'info'
                        //     showText = "ISS16!!!!";
                        // }
                        swal({
                            type: 'warning',
                            title: 'ISS16: '+ '<span class="import">'+result.value+'</span>',
                            html: '<p style="font-size:8px; color:#AAAAAA;"></p>',
                            animation: false,
                            customClass: 'animated zoomIn'
                        })
                    }
                })
        }
    })
};




